<!--
 * @Descripttion: 项目维护列表
 * @version: 
 * @Author: 曾元球
 * @Date: 2019-06-20 09:05:19
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-12 15:12:38
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">项目维护</div>
      <div class="head-btn"
        v-if="btns.hasInsert"
        data-btn-id='106005002'
        @click="openDialog('insert')">
        <img src="@/assets/img/add.png"
          alt="新增">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='项目名称：'>
          <el-input placeholder="请输入"
            v-model="querys.pname"
            clearable />
        </el-form-item>
        <el-form-item label='所属机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>
        <el-form-item label="显示状态："
          style="float:right;margin-right:0">
          <el-radio-group class="red"
            v-model="querys.enabled"
            @change="$refs.pagination.initPageList(1)">
            <el-radio label="">全部</el-radio>
            <el-radio label="1">已启用</el-radio>
            <el-radio label="2">已禁用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label="机构名称"
          prop="oname"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='项目顺序'
          prop='sortNum'
          width='100'></el-table-column>
        <el-table-column label='项目编号'
          prop='sysGuid'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='项目名称'
          prop='pname'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='科室状态'
          width="100">
          <template v-slot="{row}">
            <span :class="[row.enabled == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='创建人员'
          prop="createUserName"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='创建日期'
          width='120'>
          <template v-slot="{row}">
            {{row.createDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template v-slot="{row}">
            <i class="iconfont bj-search"
              @click="openDialog('see', row)"></i>
            <i class="iconfont bj-edit"
              v-if="handleBtnShow(row, 'hasUpdate')"
              data-btn-id='106005003'
              @click="openDialog('update', row)"></i>
            <i class="iconfont bj-delete"
              v-if="handleBtnShow(row, 'hasDel')"
              data-btn-id='106005004'
              @click="delConfirm(row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination queryUrl='/sys/project/listPaging'
        :queryObj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination' />
    </div>

    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='500px'
      :title="dialogTitles[dialogType]"
      :close-on-click-modal='false'
      center>
      <template v-if='dialogType==="see"'>
        <div class="flex">
          <el-form size="small"
            class="see-style"
            label-width="auto">
            <el-form-item label="机构名称：">
              {{dataInfo.oname}}
            </el-form-item>
            <el-form-item label="科室名称：">
              {{dataInfo.deptName}}
            </el-form-item>
            <el-form-item label="项目名称：">
              {{dataInfo.pname}}
            </el-form-item>
            <el-form-item label="项目编号：">
              {{dataInfo.sysGuid}}
            </el-form-item>
            <el-form-item label="项目顺序：">
              {{dataInfo.sortNum}}
            </el-form-item>
            <el-form-item label="结果类型：">
              {{
                dataInfo.resultType == 1 ? '数值型' :
                dataInfo.resultType == 2 ? '文本型(医技检查)' :
                '文本型(科室检查)'
              }}
            </el-form-item>
            <el-form-item label="拼音简码：">
              {{dataInfo.pySimpleCode}}
            </el-form-item>
            <el-form-item label="五笔简码：">
              {{dataInfo.wbSimpleCode | emptyData}}
            </el-form-item>
            <el-form-item label="状态：">
              {{dataInfo.enabled == 1 ? '启用' : '禁用'}}
            </el-form-item>
          </el-form>
        </div>
        <div slot='footer'
          v-if="btns.hasUpdate"
          data-btn-id='106005003'>
          <el-button class="btn-info"
            @click="openDialog('update')">修改</el-button>
        </div>
      </template>
      <template v-else>
        <el-form size="small"
          label-width="auto"
          :model="formFields"
          :rules="rules"
          ref='form'>

          <!-- 更改为 不能选择其它机构 -->
          <!-- <el-form-item label="机构名称："
            prop="organizationGuid">
            <el-input v-if="dialogType==='update'"
              disabled
              :value="dataInfo.oname"></el-input>
            <el-select class="block ctrl-height"
              v-else
              v-model="formFields.organizationGuid"
              value-key='id'
              @change='onOrgChange'>
              <el-option v-for="item in orgList"
                :key='item.sysGuid'
                :value="{id:item.sysGuid,name:item.oname}"
                :label="item.oname" />
            </el-select>
          </el-form-item> -->
          <el-form-item label="科室名称："
            prop="deptId">
            <el-input v-if="dialogType==='update'"
              disabled
              :value="dataInfo.deptName"></el-input>
            <el-select class="block ctrl-height"
              v-else
              v-model="formFields.deptId"
              :disabled="dialogType==='update'">
              <el-option v-for="item in deptList"
                :value="item.sysGuid"
                :label="item.dname"
                :key='item.sysGuid' />
            </el-select>
          </el-form-item>
          <el-form-item label="项目名称："
            prop="pname">
            <el-input placeholder="请输入"
              v-model="formFields.pname"
              maxlength="51"
              :disabled="dialogType==='update'" />
          </el-form-item>
          <el-form-item label="项目顺序："
            prop="sortNum">
            <el-input placeholder="请输入"
              :value="formFields.sortNum"
              @input="$outInt($event, 'sortNum', formFields)"
              maxlength="5" />
          </el-form-item>
          <el-form-item label="结果类型："
            prop="resultType">
            <el-select class="block"
              v-model="formFields.resultType">
              <el-option value="1"
                label="数值型" />
              <el-option value="2"
                label="文本型(医技检查)" />
              <el-option value="3"
                label="文本型(科室检查)" />
            </el-select>
          </el-form-item>
          <el-form-item label="拼音简码："
            prop="pySimpleCode">
            <el-input placeholder="请输入"
              v-model="formFields.pySimpleCode"
              maxlength="51" />
          </el-form-item>

          <el-form-item label="五笔简码："
            prop="wbSimpleCode">
            <el-input placeholder="请输入"
              v-model="formFields.wbSimpleCode"
              maxlength="51" />
          </el-form-item>
          <el-form-item label="状态："
            prop="enabled">
            <el-select class="block"
              v-model="formFields.enabled">
              <el-option value="1"
                label="启用"></el-option>
              <el-option value="2"
                label="禁用"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-hollowing"
            @click="dialogIsShow = false">取消</el-button>
          <el-button class="btn-info"
            @click="onSubmit"
            :loading="submitLoading">保存</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { lenRule } from '@/lib/utils'
import { mapState } from 'vuex'
const MAX_PROJECT = '1' // 大项目类型(此页面维护大项目)
export default {
  name: 'ProjectList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      querys: {
        pname: '',
        enabled: '',
        createOrganizationId,
        projectType: MAX_PROJECT
      },
      pageNum: 1,
      pageSize: 10,
      formFields: {
        deptId: '',
        pname: '',
        sortNum: '',
        pySimpleCode: '',
        wbSimpleCode: '',
        enabled: '1',
        resultType: '',
        projectType: MAX_PROJECT
      },
      rules: {
        organizationGuid: { required: true, message: '请选择所属机构' },
        deptId: { required: true, message: '请选择所属科室' },
        sortNum: { required: true, message: '请输入项目顺序', trigger: 'blur' },
        pname: [
          {
            required: true,
            message: '请输入项目名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        pySimpleCode: [
          {
            required: true,
            message: '请输入拼音简码',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        resultType: [{ required: true, message: '请选择结果类型', trigger: 'change' }],
        wbSimpleCode: { max: 50, message: '最多输入50个字符' }
      },
      dataInfo: {},
      deptList: [],
      dialogIsShow: false,
      dialogType: '',
      loading: true,
      submitLoading: false
    }
  },

  created() {
    this.initStatic()
  },

  methods: {
    initStatic() {
      this.dialogTitles = { see: '查看', insert: '新增', update: '修改' }
    },

    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    delConfirm({ sysGuid }) {
      this.$confirm('', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/sys/project/remove', { sysGuid })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length === 1) {
                let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
                this.$refs.pagination.initPageList(pageNum)
              } else {
                this.$refs.pagination.initPageList()
              }
            })
            .catch(err => {
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },

    // 打开弹框
    openDialog(type, data) {
      this.getDept() // 更改
      this.dialogIsShow = true
      this.dialogType = type
      if (data) {
        this.dataInfo = data
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = data[key]
        })
        this.formFields.sysGuid = data.sysGuid
      } else if (type === 'insert') {
        Object.keys(this.formFields).forEach(key => {
          if (key !== 'projectType') {
            this.formFields[key] = ''
          }
        })
        this.formFields.enabled = '1'
      }
      this.$nextTick(_ => {
        this.dialogType !== 'see' && this.$refs.form.clearValidate()
      })
    },

    // 提交新增和更新
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        let url = `/sys/project/${this.dialogType}`
        let msg = this.dialogTitles[this.dialogType]

        // this.handleFields()
        this.$post(url, this.formFields)
          .then(res => {
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },

    // 不用处理
    // handleFields(fields) {
    //   let org = fields.organizationGuid
    //   return { ...fields, organizationGuid: org.id, organizationName: org.name }
    // },

    // 不能选择其它机构
    // onOrgChange({ id }) {
    //   this.formFields.deptId = ''
    //   this.$nextTick(() => this.$refs.form.clearValidate('deptId'))
    //   this.getDept(id)
    // },

    // 获取当前机构下的科室
    getDept(organizationGuid) {
      // 更改
      if (this.deptList.length) return
      this.$post('/sys/dept/listPaging', {
        pageNum: '1',
        pageSize: '10000',
        enabled: '1',
        organizationGuid: this.orgGuid // 更改
      })
        .then(({ data }) => {
          if (!data) return
          this.deptList = data.list
        })
        .catch(err => this.$message.error(err))
    },

    handleBtnShow(row, btnType) {
      let { organizationGuid, enabled } = row,
        isCurrentOrg = organizationGuid === this.orgGuid,
        isEnabled = enabled == 1
      return isCurrentOrg && isEnabled && this.btns[btnType]
    }
  },
  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasStatus: ~btnIds.indexOf('106005001'), // 更改状态权限
        hasInsert: ~btnIds.indexOf('106005002'), // 新增按钮权限
        hasUpdate: ~btnIds.indexOf('106005003'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106005004') // 删除按钮权限
      }
    },

    orgGuid() {
      return this.$store.getters.o_userinfo.orgGuid
    },
    ...mapState(['orgList'])
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.see-style {
  margin: 0 auto;
  max-width: 100%;
}
.el-select.ctrl-height /deep/ .el-input {
  max-height: 32px;
}
</style>



